import chai from 'chai';
import chaiHttp from 'chai-http';
import UserhelperClass from '../../helpers/users/user';
import dataStore from '../../dummy/dummy';


chai.use(chaiHttp);
chai.expect();

describe('UserController helperClasses tests', () => {

    /*
   * Test the User Helper class
   */
    describe('USER HELPER CLASS', () => {
        it('should return -1 once user request is not found in array', (done) => {
            let helper = UserhelperClass.findUser(dataStore, 10);
            expect(helper).to.be.equal(-1);
            done();
        });

        it('should return position of user request in array', (done) => {
            let helper = UserhelperClass.findUser(dataStore, 1);
            expect(helper).toBeGreaterThan(-1);
            done();
        });

    });




});